import { fetchGetInstanceList } from '@/api/demo/index.js'
export default {
  methods: {
    /**
     * 获取实例列表
     * @param {*} type ：upf
     * @returns 
     */
    getServiceInstance (type) {
      return new Promise((resolve, reject) => {
        let param = {
          serviceName: type
        }
        fetchGetInstanceList(param).then((res) => {
          console.log('获取的实例列表', res)
          resolve(res)
        }).catch((e) => {
          reject(e)
          console.log('e', e)
        })
      })
    },
  }
}
