        >
          <template v-for="(value, key) in trafficTimer">
            <el-form-item :label="trafficTimerType[key].txt" :key="key">
              <el-input
                v-if="trafficTimerType[key] && trafficTimerType[key].type && (trafficTimerType[key].type === 'string' ||
                    trafficTimerType[key].type === 'int')"
                v-model="trafficTimer[key]"
                class="input-data"
              ></el-input>
            </el-form-item>
          </template>
          <el-form-item>
            <el-button type="primary" @click="onSubmitSet('trafficTimer')"
              >添加/修改</el-button
            >
          </el-form-item>
        </el-form>
      </el-tab-pane>

      <el-tab-pane label="业务规格配置" :lazy="true" name="specification">
        <el-form
          ref="specification"
          :model="specification"
          label-width="220px"
        >
          <template v-for="(value, key) in specification">
            <el-form-item :label="specificationType[key].txt" :key="key">
              <el-input
                v-if="specificationType[key] && specificationType[key].type &&
                  (specificationType[key].type === 'string' ||
                    specificationType[key].type === 'int')"
                v-model="specification[key]"
                class="input-data"
              ></el-input>
            <el-select
              v-if="specificationType[key].type && specificationType[key].type === 'select'"
              v-model="specification[key]"
              placeholder="请选择路由id"
            >
            <el-option
                v-for="item in [{value: 4000000}, {value: 8000000}]"
                :key="item.value"
                :label="item.value"
                :value="item.value"
              >
              </el-option>
            </el-select>
            </el-form-item>
          </template>
          <el-form-item>
            <el-button type="primary" @click="onSubmitSet('specification')"
              >添加/修改</el-button
            >
          </el-form-item>
        </el-form>
      </el-tab-pane>

      <el-tab-pane label="路由配置" :lazy="true" name="route config">
        <routeConfig
          :instanceValue="instanceValue"
          :data="routeConfig"
          type="route config"
          :formData="route_config"
          :routeConfigData="routeData"
        ></routeConfig>
      </el-tab-pane>
    </el-tabs>
  </d2-container>
</template>

<script>
import mixinGetInstanceListMixin from "./mixinGetInstanceList";
import { fetchGetConfig, fetchSetConfig } from "@/api/demo/index.js";
import { Message } from "element-ui";
import EventBus from "./eventbus";
import {
  cpeType,
  cpeN4Type,
  dpeType,
  flowTimeoutType,
  route_table,
  network_interface,
  nwi_config,
  network_instance,
  default_network_instance,
  pfcp_endpoint,
  gtpu_endpoint,
  applications,
  ipPool_data,
  traffic_timer,
  specificationType,
  route_config
} from "./config.js";
import {
  cpeN4Set,
  cpeSet,
  dpeSet,
  trafficTimerSet,
  specificationSet
} from './configSet'
import routeConfig from "./routeConfig";
import instenceSelect from '../compoents/instenceSelect.vue';
export default {
  mixins: [mixinGetInstanceListMixin],
  name: "upf",
  components: {
    routeConfig,
    instenceSelect
  },
  data() {
    return {
      options: [], // upf下面的实例配置
      instanceValue: "", // 当前选中的实例
      tab: "route table", // 当前的tab
      tabLoading: false,
      cpeType: cpeType, //cpe属性
      cpeN4Type: cpeN4Type, //cpe n4属性
      dpeType: dpeType, //dpe属性
      trafficTimerType: traffic_timer, // 业务定时器配置
      specificationType, // 业务规格配置
      flowTimeoutType: flowTimeoutType, //flowTimeoutType 属性
      cpe: {
        max_session: "",
        max_incoming_req: "",
        max_outcoming_req: "",
        heartbeat_interval: "",
        inactivity_timer: "",
        n1_request: "",
        t1_response_timeout: "",
      },
      cpeN4: {
        pfcp_local_ipv4_address: "",
        pfcp_local_ipv6_address: "",
      },
      dpe: {
        dpe_number: "",
        dpe_max_session: "",
        dpe_flow_factor: "",
        dp_accelerate: false,
        dpe_flow_count_per_session: "",
        dpe_flow_idle_time_per_session: "",
        n3_interface_ipv4_address: "",
        n6_interface_ipv4_address: "",
        n9_interface_ipv4_address: "",
        n3_interface_ipv6_address: "",
        n6_interface_ipv6_address: "",
        n9_interface_ipv6_address: "",
      },
      //业务定时器配置
      trafficTimer: {
        heartbeat_interval: '' ,
        heartbeat_fail_count: '',
        msg_resend_interval: '',
        msg_resend_times: '',
        flow_timeout_ipv4: '',
        flow_timeout_ipv6: '',
        flow_timeout_icmp: '',
        flow_timeout_udp: '',
        flow_timeout_tcp: '',
        msg_resend_fail_count: '',
      },
      //业务规格配置
      specification: {
        max_assoc: '',
        max_session: '',
        max_thp: '',
        max_qos: 4000000
      },

      //关于数据的获取，全是数组格式的通用表格组件,添加数据的时候通用数据组件
      routeData: [], //路由表配置数据
      netInterfaceData: [], //网络接口配置数据
      nwiData: [], //数据接口配置数据
      networkInstanceData: [], //网络实例接口配置数据
      dNetworkInstanceData: [], //默认实例接口配置数据
      pfcpendpointData: [], //PFCP端点配置
      gtpuEndpointData: [], //dtpu端点配置
      applicationsData: [], //应用端点配置
      ipPoolData: [], //IP地址池配置
      //路由表配置
      routeConfig: [],

      route_table: route_table,
      net_interface: network_interface,
      nwi: nwi_config,
      network_instance,
      default_network_instance,
      pfcp_endpoint,
      gtpu_endpoint,
      applications,
      ipPool_data,
      route_config, // 路由表配置
    };
  },
  created() {
    // this.getUpfServiceInstance();
    let that = this;
    EventBus.$on("fetchGetConfig", function (type) {
      console.log("eventbusgettype", type);
      that.getUpfManage();
    });
  },
  methods: {
    // 获取当前upf下面的实例
    getUpfServiceInstance() {
      this.getServiceInstance("upf")
        .then((res) => {
          this.options = res.hosts;
          if (this.options.length > 0) {
            this.instanceValue = this.options[0].instanceId;
            this.getUpfManage();
            //默认还需要获取router的内容，其他tab有用到
            this.getUpfManage("route table");
          }
        })
        .catch((e) => {
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    //下拉upf获取数据
    getUpfSelect(val){
      this.instanceValue = val
      this.getUpfManage();
    },
    // 获取当前的DPE和CPE管理以及其他配置的内容
    getUpfManage(type) {
      if (this.instanceValue == "") {
        this.$message.warning("请先选择实例");
        return;
      }
      this.tabLoading = true;
      console.log("当前选中的tab,", this.tab);
      console.log("routerConfi", this.routeData);
      let data = {
        request_type: (type || this.tab) + " get",
        instance_id: this.instanceValue,
        sequence_no: 1000,
      };
      //请求实例列表
      fetchGetConfig(data)
        .then((res) => {
          console.log("此时请求列表res", res);
          this.tabLoading = false;
          if (res.status === true) {
            this.dealConfig(res.data);
            if (type) {
              this.dealConfig(res.data, type);
            }
          } else {
            this.dealConfig([], type);
            this.$message.error(res.error || res.msg || "网络错误，请稍后再试" + res);
          }
        })
        .catch((e) => {
          this.dealConfig([], type);
          this.tabLoading = false;
          console.log("error:", e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    /**
     * 处理配置的数据
     */
    dealConfig(data, type) {
      let switchData = type || this.tab;
      let emptyData = [];
      if(data && Array.isArray(data) && !data.length) {
        emptyData = ''
      }
      switch (switchData) {
        case "cpe configuration":
          this.cpe = emptyData ? data[0] : {...cpeSet};
          break;
        case "dpe configuration":
          this.dpe = emptyData ?  data[0] : {...dpeSet};
          break;
        case "route table":
          this.routeData = emptyData ?  data : [];
          console.log("===>type", type);
          break;
        case "net interface":
          this.netInterfaceData = emptyData ?  data : [];
          break;
        case "nwi":
          this.nwiData = emptyData ?  data : [];
          break;
        case "network instance":
          this.networkInstanceData = emptyData ?  data : [];
          break;
        case "default network instance":
          this.dNetworkInstanceData = emptyData ?  data : [];
          break;
        case "pfcp endpoint":
          this.pfcpendpointData = emptyData ?  data : [];
          break;
        case "gtpu endpoint":
          this.gtpuEndpointData = emptyData ?  data : [];
          break;
        case "applications":
          //应用规则有数组，要把数组里的拿出来显示
          let applicationsData = [];
          // data.map((item) => {
          //   if(item.rules && item.rules.length) {
          //     item.rules.map((ele) => {
          //       applicationsData.push({
          //         name: item.name,
          //         ...ele
          //       })
          //     })
          //   }
          // })
          this.applicationsData = emptyData ?  data : [];
          break;
        case "ip pool":
          this.ipPoolData = emptyData ?  data : [];
          break;
        case "flow timeout":
          this.flowTimeout = emptyData ?  data : [];
          console.log(data);
          break;
        case "cpe n4 listen address":
          this.cpeN4 = emptyData ?  data[0] : {...cpeN4Set};
          console.log(data);
          break;
        case "specification":
          this.specification = emptyData ?  data : {...specificationSet};
          break;
        case "traffic timer": 
          this.trafficTimer = emptyData ?  data : {...trafficTimerSet};
          break;
        case "route config": 
          this.routeConfig = emptyData ?  data : [];
          break;
      }
    },
    /**
     * 切换tab
     * tab: cpe, dpe
     */
    clickTab(tab) {
      console.log("tab--", tab);
      console.log("切换的tab", this.tab);
      this.getUpfManage();
    },
    /**
     * cpe和dpe保存
     */
    onSubmitCpeDpe() {
      console.log("cpe", this.tab);
      let setData = "";
      if (this.tab === "cpe configuration") {
        for (let i in this.cpe) {
          if (this.cpe[i] === "") {
            Message({
              message: "请填入" + this.cpe[i],
              type: "error",
            });
            return;
          }
          if (Number(this.cpe[i]) === "NaN") {
            Message({
              message: this.cpe[i] + "请输入数字类型",
              type: "error",
            });
            return;
          }
          this.cpe[i] = Number(this.cpe[i]);
        }
        setData = this.cpe;
      } else if (this.tab === "cpe n4 listen address") {
        for (let i in this.cpeN4) {
          if (this.cpeN4[i] === "") {
            Message({
              message: "请填入" + this.cpeN4[i],
              type: "error",
            });
            return;
          }
        }
        setData = this.cpeN4;
      } else {
        for (let i in this.dpe) {
          if (
            (this.dpeType[i].type === "int" ||
              this.dpeType[i].type === "string") &&
            this.dpe[i] === ""
          ) {
            Message({
              message: "请填入" + this.dpe[i],
              type: "error",
            });
            return;
          }
          if (this.dpeType[i].type === "int" && Number(this.dpe[i]) === "NaN") {
            Message({
              message: this.dpe[i] + "请输入数字类型",
              type: "error",
            });
            return;
          }
          if (this.dpeType[i].type === "int" && Number(this.dpe[i]) !== "NaN") {
            this.dpe[i] = Number(this.dpe[i]);
          }
        }
        setData = this.dpe;
      }
      let params = {
        request_type: this.tab + " set",
        instance_id: this.instanceValue,
        sequence_no: 1000,
        data: [setData],
      };
      fetchSetConfig(params).then((res) => {
        if (res.status === true) {
          this.$message.success("设置成功");
          console.log("获取的dpe和cpe", this[this.tab]);
        } else {
          this.$message.error(res.error || res.msg || "网络错误，请稍后再试");
        }
      });
    },
    /**
     * 流超时配置保存
     */
    onSubmitSet(type) {
      let objData = this[type];
      for (let i in this[type]) {
        if (objData[i] === "") {
          Message({
            message: "请填入" + objData[i],
            type: "error",
          });
          return;
        }
        if (Number(objData[i]) === "NaN") {
          Message({
            message: objData[i] + "请输入数字类型",
            type: "error",
          });
          return;
        }
        objData[i] = Number(objData[i]);
      }

      let params = {
        request_type: this.tab + " set",
        instance_id: this.instanceValue,
        sequence_no: 1000,
        data: objData,
      };
      fetchSetConfig(params).then((res) => {
        if (res.status === true) {
          this.$message.success("设置成功");
        } else {
          this.$message.error(res.error || res.msg || "网络错误，请稍后再试");
        }
      });
    },
  },
};
</script>
<style  scoped>
.input-data {
  width: 220px;
}
</style>
