<template>
  <div>
    <div style="margin-bottom: 10px">
      <el-button type="primary" @click="addData">添加数据</el-button>
    </div>
    <el-table stripe border :data="data" v-if="data.length > 0">
      <!-- <el-table-column
        :key="key"
        v-for="(item, key) in formData"
        :prop="key"
        :label="item.txt"
      ></el-table-column> -->
      <template v-if="type == 'applications'">
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-table stripe border :data="scope.row.rules" v-if="scope.row.rules.length > 0">
              <el-table-column label="规则ID" prop="id"></el-table-column>
              <el-table-column label="规则类型" prop="rule_type"></el-table-column>
              <el-table-column label="规则内容" prop="rule"></el-table-column>
            </el-table>
          </template>
        </el-table-column>
        <el-table-column
          label="应用名称"
          prop="name">
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" type="danger" @click="delData(scope.row)"
            >删除</el-button
            >
          </template>
        </el-table-column>
      </template>

      <template v-else>
        <el-table-column
          :key="item"
          v-for="item in columnData"
          :prop="item"
          :label="
            formData[toLowerCaseTxt(item)]
            ? formData[toLowerCaseTxt(item)].txt
            : item
          "
        ></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" type="danger" @click="delData(scope.row)"
            >删除</el-button
            >
          </template>
        </el-table-column>
      </template>

    </el-table>
    <!-- 添加数据的表格 -->
    <el-dialog :title="label" :visible.sync="isShowDiaLog">
      <el-form :modal="formItem" v-if="type !== 'applications'">
        <el-form-item
          :label="formData[key].txt"
          v-for="(value, key) in formItem"
          :key="key"
        >
          <!-- v-if="formData[key].type == 'string' || formData[key].type == 'string'" -->

          <!-- 选择路由表中的配置 -->
          <template v-if="isTableId(key)">
            <el-select
              :value="formItem[key]"
              placeholder="请选择路由id"
              @change="onInput($event, key)"
            >
              <el-option
                v-for="item in routeConfigData"
                :key="item.id"
                :label="item.id"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </template>

          <template v-else-if="type=== 'gtpu endpoint' && formData[key].type && formData[key].type == 'select'">
            <el-select
              :value="formItem[key]"
              placeholder="请选择源接口类型"
              @change="onInput($event, key)"
            >
              <el-option
                v-for="(subItem, subIndex) in [
                  'access',
                  'core',
                  'cp',
                  'n9core',
                ]"
                :key="subIndex"
                :label="subItem"
                :value="subItem"
              >
              </el-option>
            </el-select>
          </template>

          <template v-else-if="type === 'net interface' && key === 'ip_prefix'">
            <div class="prefix-data">
              <div class="prefix-item" v-for="(item, index) in formItem.ip_prefix" :key="index">
                 <label>ip地址{{index+1}}:</label>
                 <el-input v-model="item.ip"></el-input>
              </div>
              <el-button class="button-rule" @click="addIpPrefix">添加ip地址</el-button>

            </div>
          </template>

          <template v-else>
            <el-input
              :value="formItem[key]"
              @input="onInput($event, key)"
            ></el-input>
          </template>

        </el-form-item>
      </el-form>


      <div v-else>
        <div class="input-application">
        <label>应用名称</label>
        <el-input
          :value="formItem['name']"
          @input="onInput($event, 'name')"
        ></el-input>
        </div>

        <div v-for="(item, index) in formItem.rules" :key="index">
          <div class="rule-application">{{"规则"+(index+1)}}<i v-if="formItem.rules.length > 1" @click="delRules(index)" class="el-icon-delete"></i></div>
          <div>
            <div class="input-application">
              <label>规则ID</label>
              <el-input-number v-model="item.id"></el-input-number>
            </div>
            <div class="input-application">
              <label>规则类型</label>
              <el-select
              v-model="item.rule_type"
              placeholder="请选择源接口类型"
            >
              <el-option
                label="regex"
                value="regex"
              >
              </el-option>
              <el-option
                label="ipfilter"
                value="ipfilter"
              >
              </el-option>
            </el-select>
              <!-- <el-input v-model="item.rule"></el-input> -->
            </div>
            <div class="input-application">
              <label>规则内容</label>
              <el-input v-model="item.rule"></el-input>
            </div>
          </div>
        </div>
        <el-button class="button-rule" @click="addRules">添加应用规则</el-button>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleCancel">取 消</el-button>
        <el-button type="primary" @click="handleConfirm">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { fetchSetConfig } from "@/api/demo/index.js";
import EventBus from "./eventbus";
export default {
  data() {
    return {
      columnData: {}, //表格显示的数据，因为表格显示的和传的不一样，所以不能共用，只能等到有数据才会显示
      isShowDiaLog: false, //显示添加数据的弹窗
      formItem: {}, //添加的数据
      initFormItem: {}, //默认添加的数据，再添加完后需要置为初始化的
    };
  },
  watch: {
    data: {
      handler(val) {
        if (
          Array.isArray(val) &&
          val.length &&
          val[0] instanceof Object &&
          !Object.keys(this.columnData).length
        ) {
          this.columnData = Object.keys(val[0]);
        }
      },
      immediate: true,
    },
    routeConfigData: {
      handler(val) {
        console.log("此时的routeConfigData", val);
      },
    },
    formData: {
      handler(val) {
        let data = Object.keys(val); //配置项的数据
        
        if (val && data.length) {
          if(this.type === 'applications') {
            this.formItem['name'] = '';
            this.formItem['rules'] = [{
              id: '',
              rule_type: '',
              rule: ''
            }]
          } else if (this.type === 'net interface') {
            data.map((item) => {
              if(item === 'ip_prefix') {
                this.formItem['ip_prefix'] = [{
                  ip: ''
                }]
                console.log('网络接口prefix', this.formItem['ip_prefix'])
              } else {
                this.formItem[item] = "";
                console.log('item', item)
              }
            });
            
          }else {
            data.map((item) => {
              this.formItem[item] = "";
            });
          }
          this.initFormItem = { ...this.formItem };
        }
      },
      immediate: true,
    },
  },
  props: {
    type: {
      //tab
      default: "",
      type: String,
    },
    //添加的数据表单
    formData: {
      type: Object,
      default() {
        return {};
      },
    },
    //instanceValue
    instanceValue: {
      default: "",
      type: String,
    },
    //sequenceNo
    sequenceNo: {
      type: Number,
      default: 1000,
    },
    //中文tab
    label: {
      default: "",
      type: String,
    },
    //表格显示的数据
    data: {
      type: Array,
      default() {
        return [];
      },
    },
    //路由表
    routeConfigData: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  created() {},
  mounted() {},
  methods: {
    //后端接口竟然大小写夹杂着，写的太烂了吧
    toLowerCaseTxt(data) {
      return data.toLowerCase();
    },
    isObject(data) {
      return data instanceof Object;
    },
    //判断是不是路由表，就需要可选数据
    isTableId(id) {
      let type = ["net interface", "nwi", "pfcp endpoint", "route config"]; //只有这几个tab下需要路由表里的数据
      let typeData = ["table_id_v4", "table_id_v6", "table_id"];
      if (type.indexOf(this.type) > -1 && typeData.indexOf(id) > -1) {
        console.log('===>匹配type', this.type+'tableId=='+id);
        return true;
      }
      
      return false;
    },

    //删除数据
    delData(row) {
      let data = [row]
      if(this.type === 'applications') {
        data =[{name: row.name}]
      }
      let params = {
        request_type: this.type + " del",
        instance_id: this.instanceValue,
        sequence_no: 1000,
        data: data,
      };
      fetchSetConfig(params).then((res) => {
        if (res.status === true) {
          this.$message.success("删除成功");
          EventBus.$emit("fetchGetConfig", this.type);
        } else {
          this.$message.error(res.error || res.msg || "网络错误，请稍后再试");
        }
      });
    },
    //增加数据
    addData() {
      if (this.instanceValue == "") {
        this.$message.warning("请先选择实例");
        return;
      }
      this.isShowDiaLog = true;
    },
    //确定按钮
    handleConfirm() {
      let curFormItem = {}
      for (let key in this.formItem) {
        console.log("key", key);
        if ((this.formData[key].type == "int" || this.formData[key].type == "string" ) && (this.formItem[key] == "" || this.formItem[key].trim == "")) {
          this.$message.error("请填写完数据再提交");
          return;
        }
        //判断数字类型
        if (this.formData[key].type == "int") {
          let data = Number(this.formItem[key]);
          if (isNaN(data)) {
            this.$message.error(`${key}请填写int类型提交`);
            return;
          }
          this.formItem[key] = data;
        }
        //单独处理接口ip地址
        if(this.formData[key].type == "array" && key === 'ip_prefix') { 
            let ip_prefix = []
            if(this.formItem.ip_prefix.length) {
              this.formItem.ip_prefix.map((ele) => {
                if(ele.ip) {
                  ip_prefix.push(ele.ip)
                }
              })
            }
            if(!ip_prefix.length) {
              this.$message.error(`接口ip地址至少请填写一个`);
              return
            }
            curFormItem.ip_prefix =  ip_prefix
        }
        //单独处理下应用规则的rules数组
        // if(this.type === 'applications' && key === 'rules') {
        //   if(this.formItem.rules.length) {
        //     this.formItem.rules.map((ele) => {
        //       if(ele.id) {
        //         let data = Number(ele.id);
        //         if (isNaN(data)) {
        //           this.$message.error(`${key}请填写int类型提交`);
        //           return;
        //         }
        //         ele.id = data
        //       }
        //       return ele
        //     })
        //   }
        // }
      }

      curFormItem = {...this.formItem, ...curFormItem}
      let params = {
        request_type: this.type + " add",
        instance_id: this.instanceValue,
        sequence_no: this.sequenceNo,
        data: [curFormItem],
      };
      fetchSetConfig(params).then((res) => {
        if (res.status === true) {
          this.$message.success("添加成功");
          this.formItem = { ...this.initFormItem };
          this.isShowDiaLog = false;
          EventBus.$emit("fetchGetConfig", this.type);
        } else {
          this.$message.error(res.error || res.msg || "网络错误，请稍后再试");
        }
      });
    },
    //判定是否是数字类型
    isNumber(data){

    },
    //取消按钮
    handleCancel() {
      this.formItem = { ...this.initFormItem };
      if(this.type === 'applications') { //因为applications有数组，会带上
        this.formItem['rules'] = [{
          id: '',
          rule_type: '',
          rule: ''
        }]
      }

      if(this.type === 'net interface') { //同上
        this.formItem['ip_prefix'] = [{
          ip: ''
        }]
      }
      
      this.isShowDiaLog = false;
    },
    //数据监听，形成双向绑定，这里不能使用v-model
    onInput(value, name) {
      this.formItem[name] = value;
      this.formItem = Object.assign({}, this.formItem);
    },
    //添加应用规则
    addRules(){
      this.formItem.rules.push({
        id: '',
        rule_type: '',
        rule: ''
      })
      this.formItem = Object.assign({}, this.formItem);
    },
    //删除应用规则
    delRules(index){
      this.formItem.rules.splice(index, 1)
    },
    //添加网络接口配置的ip地址
    addIpPrefix(){
      this.formItem.ip_prefix.push({
        ip: '',
      })
      this.formItem = Object.assign({}, this.formItem);
      console.log('===>ip_prefix', this.formItem)
    }
  },
};
</script>

<style lang="scss" scoped>
.input-application {
  display: flex;
  margin-bottom: 10px;
  label {
    width: 80px;
    text-align: left;
  }
}
.rule-application {
  margin: 20px 0;
}
.button-rule {
  margin-top: 20px;
}
.prefix-data {
  margin-top: 33px;
}
.prefix-item {
  display: flex;
  width: 100%;
  label {
    width: 66px;
  }
}
</style>
